import { observer } from "mobx-react-lite";
import { Field, Formik } from "formik";
import * as Yup from "yup";

import emailStore from "stores/emailStore";

import marker_icon from "assets/img/marker_icon.svg";
import phone_icon from "assets/img/phone_icon.svg";
import letter_icon from "assets/img/letter_icon.svg";
import classes from "./contact.module.scss";

const schema = Yup.object().shape({
  name: Yup.string().required("Name is required!"),
  email: Yup.string().required("Email is required!"),
  subject: Yup.string().required("Subject is required!"),
  comments: Yup.string().required("Comments is required!"),
});

const ContactUsPage = () => {
  const { sendEmail } = emailStore;

  const contactInfo = [
    {
      name: "Address",
      value: "PO Box 560295 Rockledge, Fl 32956",
      icon: marker_icon,
    },
    {
      name: "Phone",
      value: "(720) 822-5372",
      icon: phone_icon,
    },
    {
      name: "E-mail",
      value: "info@archsoftware.com",
      icon: letter_icon,
    },
  ];

  const onSubmit = (values, { resetForm }) => {
    console.log(values)
    const payload = {
    ...values,
    to_email: "arch.software.dev@gmail.com",
  };
    sendEmail({ payload, callback: () => resetForm() });
  };

  return (
    <div className={classes.contactWrapper}>
      <div className="container rowGap-40">
        <h4 className="title text-center">Get in Touch</h4>
        <div className={classes.contactContent}>
          <div className="rowGap-30">
            <div className={classes.contactHeader}>
              <p className="title size-25">Reach out to our office at</p>
            </div>
            <div className={`${classes.contactUsInfo} rowGap-20`}>
              {contactInfo.map((info, i) => (
                <span key={i} className="paragraph-black size-18">
                  <img src={info?.icon} alt="icon" />
                  {info.name}:<p className="paragraph-black">{info.value}</p>
                </span>
              ))}
            </div>
          </div>
          <Formik
            initialValues={{ name: "", email: "", subject: "", comments: "" }}
            onSubmit={onSubmit}
            validationSchema={schema}
          >
            {({ errors, handleSubmit, isValid, dirty }) => {
              return (
                <form onSubmit={handleSubmit}>
                  <div className={`${classes.formContent} rowGap-10`}>
                    <Field
                      placeholder="Name"
                      name="name"
                      helperText={errors.name}
                      error={!!errors.name}
                    />
                    <Field
                      placeholder="Email"
                      name="email"
                      helperText={errors.email}
                      error={!!errors.email}
                    />
                    <Field
                      placeholder="Subject"
                      name="subject"
                      helperText={errors.subject}
                      error={!!errors.subject}
                      as="textarea"
                      rows="3"
                    />
                    <Field
                      placeholder="Write Comments"
                      name="comments"
                      helperText={errors.comments}
                      error={!!errors.comments}
                      as="textarea"
                      inputProps={{ style: { resize: "none" } }}
                      multiline
                      rows="5"
                    />
                    <button
                      className="btn btn-light mt-30"
                      disabled={!isValid || !dirty}
                      type="submit"
                    >
                      Submit Now
                    </button>
                  </div>
                </form>
              );
            }}
          </Formik>
        </div>
      </div>
    </div>
  );
};

export default observer(ContactUsPage);
